import React from 'react';

import ImgWrapper from '../../../components/UI/ImgWrapper/ImgWrapper';

import classes from './StoryLink.module.css';

const storyPlaceHolder = (props) => (
    <div className={[classes.StoryPlaceHolder, props.className].join(" ")}>
        <ImgWrapper src={props.img_src} />
        <h3 className={classes.StoryPlaceHolderHeader}>{props.header}</h3>
    </div>
);

export default storyPlaceHolder;